import Database from 'better-sqlite3';
import path from 'path';
import fs from 'fs';
/**
 * Database service for managing SQLite operations
 * Provides a centralized interface for all database interactions
 */
export class DatabaseService {
    constructor(dbPath) {
        // Default database path in user data directory
        const defaultPath = dbPath || path.join(process.cwd(), 'database', 'teacher-assignment.db');
        // Ensure database directory exists
        const dbDir = path.dirname(defaultPath);
        if (!fs.existsSync(dbDir)) {
            fs.mkdirSync(dbDir, { recursive: true });
        }
        this.db = new Database(defaultPath);
        this.initializeDatabase();
    }
    /**
     * Get singleton instance of DatabaseService
     */
    static getInstance(dbPath) {
        if (!DatabaseService.instance) {
            DatabaseService.instance = new DatabaseService(dbPath);
        }
        return DatabaseService.instance;
    }
    /**
     * Initialize database with schema
     */
    initializeDatabase() {
        try {
            const schemaPath = path.join(process.cwd(), 'database', 'schema.sql');
            if (fs.existsSync(schemaPath)) {
                const schema = fs.readFileSync(schemaPath, 'utf8');
                this.db.exec(schema);
                console.log('Database initialized successfully');
            }
            else {
                throw new Error('Schema file not found');
            }
        }
        catch (error) {
            console.error('Failed to initialize database:', error);
            throw error;
        }
    }
    // TEACHER OPERATIONS
    /**
     * Create a new teacher
     */
    createTeacher(teacher) {
        const stmt = this.db.prepare(`
      INSERT INTO teachers (name, qualifications, working_times)
      VALUES (?, ?, ?)
    `);
        const result = stmt.run(teacher.name, JSON.stringify(teacher.qualifications), JSON.stringify(teacher.working_times));
        return this.getTeacher(result.lastInsertRowid);
    }
    /**
     * Get teacher by ID
     */
    getTeacher(id) {
        const stmt = this.db.prepare('SELECT * FROM teachers WHERE id = ?');
        const row = stmt.get(id);
        if (!row)
            return null;
        return {
            id: row.id,
            name: row.name,
            qualifications: JSON.parse(row.qualifications),
            working_times: JSON.parse(row.working_times || '{}'),
            created_at: row.created_at
        };
    }
    /**
     * Get all teachers
     */
    getAllTeachers() {
        const stmt = this.db.prepare('SELECT * FROM teachers ORDER BY name');
        const rows = stmt.all();
        return rows.map(row => ({
            id: row.id,
            name: row.name,
            qualifications: JSON.parse(row.qualifications),
            working_times: JSON.parse(row.working_times || '{}'),
            created_at: row.created_at
        }));
    }
    /**
     * Update teacher
     */
    updateTeacher(id, updates) {
        const currentTeacher = this.getTeacher(id);
        if (!currentTeacher)
            return null;
        const stmt = this.db.prepare(`
      UPDATE teachers 
      SET name = ?, qualifications = ?, working_times = ?
      WHERE id = ?
    `);
        stmt.run(updates.name ?? currentTeacher.name, JSON.stringify(updates.qualifications ?? currentTeacher.qualifications), JSON.stringify(updates.working_times ?? currentTeacher.working_times), id);
        return this.getTeacher(id);
    }
    /**
     * Delete teacher
     */
    deleteTeacher(id) {
        const stmt = this.db.prepare('DELETE FROM teachers WHERE id = ?');
        const result = stmt.run(id);
        return result.changes > 0;
    }
    // COURSE OPERATIONS
    /**
     * Create a new course
     */
    createCourse(course) {
        const stmt = this.db.prepare(`
      INSERT INTO courses (topic, lessons_count, lesson_duration, start_date, end_date)
      VALUES (?, ?, ?, ?, ?)
    `);
        const result = stmt.run(course.topic, course.lessons_count, course.lesson_duration, course.start_date, course.end_date);
        return this.getCourse(result.lastInsertRowid);
    }
    /**
     * Get course by ID
     */
    getCourse(id) {
        const stmt = this.db.prepare('SELECT * FROM courses WHERE id = ?');
        const row = stmt.get(id);
        if (!row)
            return null;
        return {
            id: row.id,
            topic: row.topic,
            lessons_count: row.lessons_count,
            lesson_duration: row.lesson_duration,
            start_date: row.start_date,
            end_date: row.end_date,
            created_at: row.created_at
        };
    }
    /**
     * Get all courses
     */
    getAllCourses() {
        const stmt = this.db.prepare('SELECT * FROM courses ORDER BY start_date');
        const rows = stmt.all();
        return rows.map(row => ({
            id: row.id,
            topic: row.topic,
            lessons_count: row.lessons_count,
            lesson_duration: row.lesson_duration,
            start_date: row.start_date,
            end_date: row.end_date,
            created_at: row.created_at
        }));
    }
    /**
     * Update course
     */
    updateCourse(id, updates) {
        const currentCourse = this.getCourse(id);
        if (!currentCourse)
            return null;
        const stmt = this.db.prepare(`
      UPDATE courses 
      SET topic = ?, lessons_count = ?, lesson_duration = ?, start_date = ?, end_date = ?
      WHERE id = ?
    `);
        stmt.run(updates.topic ?? currentCourse.topic, updates.lessons_count ?? currentCourse.lessons_count, updates.lesson_duration ?? currentCourse.lesson_duration, updates.start_date ?? currentCourse.start_date, updates.end_date ?? currentCourse.end_date, id);
        return this.getCourse(id);
    }
    /**
     * Delete course
     */
    deleteCourse(id) {
        const stmt = this.db.prepare('DELETE FROM courses WHERE id = ?');
        const result = stmt.run(id);
        return result.changes > 0;
    }
    // ASSIGNMENT OPERATIONS
    /**
     * Create a new assignment
     */
    createAssignment(assignment) {
        const stmt = this.db.prepare(`
      INSERT INTO assignments (teacher_id, course_id, scheduled_slots, status, ai_rationale)
      VALUES (?, ?, ?, ?, ?)
    `);
        const result = stmt.run(assignment.teacher_id, assignment.course_id, JSON.stringify(assignment.scheduled_slots || []), assignment.status || 'active', assignment.ai_rationale || null);
        return this.getAssignment(result.lastInsertRowid);
    }
    /**
     * Get assignment by ID
     */
    getAssignment(id) {
        const stmt = this.db.prepare(`
      SELECT a.*, t.name as teacher_name, c.topic as course_topic
      FROM assignments a
      LEFT JOIN teachers t ON a.teacher_id = t.id
      LEFT JOIN courses c ON a.course_id = c.id
      WHERE a.id = ?
    `);
        const row = stmt.get(id);
        if (!row)
            return null;
        return {
            id: row.id,
            teacher_id: row.teacher_id,
            course_id: row.course_id,
            scheduled_slots: JSON.parse(row.scheduled_slots || '[]'),
            status: row.status,
            ai_rationale: row.ai_rationale,
            created_at: row.created_at
        };
    }
    /**
     * Get all assignments with related data
     */
    getAllAssignments() {
        const stmt = this.db.prepare(`
      SELECT a.*, t.name as teacher_name, c.topic as course_topic
      FROM assignments a
      LEFT JOIN teachers t ON a.teacher_id = t.id
      LEFT JOIN courses c ON a.course_id = c.id
      ORDER BY a.created_at DESC
    `);
        const rows = stmt.all();
        return rows.map(row => ({
            id: row.id,
            teacher_id: row.teacher_id,
            course_id: row.course_id,
            scheduled_slots: JSON.parse(row.scheduled_slots || '[]'),
            status: row.status,
            ai_rationale: row.ai_rationale,
            created_at: row.created_at
        }));
    }
    /**
     * Update assignment
     */
    updateAssignment(id, updates) {
        const currentAssignment = this.getAssignment(id);
        if (!currentAssignment)
            return null;
        const stmt = this.db.prepare(`
      UPDATE assignments 
      SET teacher_id = ?, course_id = ?, scheduled_slots = ?, status = ?, ai_rationale = ?
      WHERE id = ?
    `);
        stmt.run(updates.teacher_id ?? currentAssignment.teacher_id, updates.course_id ?? currentAssignment.course_id, JSON.stringify(updates.scheduled_slots ?? currentAssignment.scheduled_slots), updates.status ?? currentAssignment.status, updates.ai_rationale ?? currentAssignment.ai_rationale, id);
        return this.getAssignment(id);
    }
    /**
     * Delete assignment
     */
    deleteAssignment(id) {
        const stmt = this.db.prepare('DELETE FROM assignments WHERE id = ?');
        const result = stmt.run(id);
        return result.changes > 0;
    }
    // SETTINGS OPERATIONS
    /**
     * Get application setting
     */
    getSetting(key) {
        const stmt = this.db.prepare('SELECT value FROM app_settings WHERE key = ?');
        const row = stmt.get(key);
        return row ? row.value : null;
    }
    /**
     * Set application setting
     */
    setSetting(key, value) {
        const stmt = this.db.prepare(`
      INSERT OR REPLACE INTO app_settings (key, value)
      VALUES (?, ?)
    `);
        stmt.run(key, value);
    }
    /**
     * Get all settings
     */
    getAllSettings() {
        const stmt = this.db.prepare('SELECT key, value FROM app_settings');
        const rows = stmt.all();
        const settings = {};
        rows.forEach(row => {
            settings[row.key] = row.value;
        });
        return settings;
    }
    // UTILITY OPERATIONS
    /**
     * Execute raw SQL query (for advanced operations)
     */
    execute(sql, params) {
        const stmt = this.db.prepare(sql);
        return params ? stmt.run(...params) : stmt.run();
    }
    /**
     * Close database connection
     */
    close() {
        if (this.db) {
            this.db.close();
        }
    }
    // WEIGHTING SETTINGS OPERATIONS
    /**
     * Create weighting settings profile
     */
    createWeightingSettings(settings) {
        const stmt = this.db.prepare(`
      INSERT INTO weighting_settings (profile_name, equality_weight, continuity_weight, loyalty_weight, is_default)
      VALUES (?, ?, ?, ?, ?)
    `);
        const result = stmt.run(settings.profile_name, settings.equality_weight, settings.continuity_weight, settings.loyalty_weight, settings.is_default ? 1 : 0);
        return this.getWeightingSettings(result.lastInsertRowid);
    }
    /**
     * Get weighting settings by ID
     */
    getWeightingSettings(id) {
        const stmt = this.db.prepare('SELECT * FROM weighting_settings WHERE id = ?');
        const row = stmt.get(id);
        if (!row)
            return null;
        return {
            id: row.id,
            profile_name: row.profile_name,
            equality_weight: row.equality_weight,
            continuity_weight: row.continuity_weight,
            loyalty_weight: row.loyalty_weight,
            is_default: row.is_default === 1,
            created_at: row.created_at
        };
    }
    /**
     * Get default weighting settings
     */
    getDefaultWeightingSettings() {
        const stmt = this.db.prepare('SELECT * FROM weighting_settings WHERE is_default = 1');
        const row = stmt.get();
        if (!row)
            return null;
        return {
            id: row.id,
            profile_name: row.profile_name,
            equality_weight: row.equality_weight,
            continuity_weight: row.continuity_weight,
            loyalty_weight: row.loyalty_weight,
            is_default: true,
            created_at: row.created_at
        };
    }
    /**
     * Get all weighting settings profiles
     */
    getAllWeightingSettings() {
        const stmt = this.db.prepare('SELECT * FROM weighting_settings ORDER BY is_default DESC, profile_name');
        const rows = stmt.all();
        return rows.map(row => ({
            id: row.id,
            profile_name: row.profile_name,
            equality_weight: row.equality_weight,
            continuity_weight: row.continuity_weight,
            loyalty_weight: row.loyalty_weight,
            is_default: row.is_default === 1,
            created_at: row.created_at
        }));
    }
    /**
     * Update weighting settings
     */
    updateWeightingSettings(id, updates) {
        const currentSettings = this.getWeightingSettings(id);
        if (!currentSettings)
            return null;
        const stmt = this.db.prepare(`
      UPDATE weighting_settings 
      SET profile_name = ?, equality_weight = ?, continuity_weight = ?, loyalty_weight = ?, is_default = ?
      WHERE id = ?
    `);
        stmt.run(updates.profile_name ?? currentSettings.profile_name, updates.equality_weight ?? currentSettings.equality_weight, updates.continuity_weight ?? currentSettings.continuity_weight, updates.loyalty_weight ?? currentSettings.loyalty_weight, (updates.is_default ?? currentSettings.is_default) ? 1 : 0, id);
        return this.getWeightingSettings(id);
    }
    /**
     * Delete weighting settings profile
     */
    deleteWeightingSettings(id) {
        const stmt = this.db.prepare('DELETE FROM weighting_settings WHERE id = ?');
        const result = stmt.run(id);
        return result.changes > 0;
    }
    // CHAT OPERATIONS
    /**
     * Save chat message to history
     */
    saveChatMessage(message) {
        const stmt = this.db.prepare(`
      INSERT INTO chat_history (conversation_id, message_type, message_content, context_data)
      VALUES (?, ?, ?, ?)
    `);
        const result = stmt.run(message.conversation_id, message.message_type, message.message_content, message.context_data || null);
        return this.getChatMessage(result.lastInsertRowid);
    }
    /**
     * Get chat message by ID
     */
    getChatMessage(id) {
        const stmt = this.db.prepare('SELECT * FROM chat_history WHERE id = ?');
        const row = stmt.get(id);
        if (!row)
            return null;
        return {
            id: row.id,
            conversation_id: row.conversation_id,
            message_type: row.message_type,
            message_content: row.message_content,
            context_data: row.context_data,
            timestamp: row.timestamp
        };
    }
    /**
     * Get all messages for a conversation
     */
    getChatMessages(conversationId) {
        const stmt = this.db.prepare(`
      SELECT * FROM chat_history 
      WHERE conversation_id = ? 
      ORDER BY timestamp ASC
    `);
        const rows = stmt.all(conversationId);
        return rows.map(row => ({
            id: row.id,
            conversation_id: row.conversation_id,
            message_type: row.message_type,
            message_content: row.message_content,
            context_data: row.context_data,
            timestamp: row.timestamp
        }));
    }
    /**
     * Save chat conversation metadata
     */
    saveChatConversation(conversation) {
        const stmt = this.db.prepare(`
      INSERT OR REPLACE INTO chat_conversations (id, title, context_snapshot)
      VALUES (?, ?, ?)
    `);
        stmt.run(conversation.id, conversation.title, JSON.stringify(conversation.context));
    }
    /**
     * Get all chat conversations
     */
    getAllChatConversations() {
        const stmt = this.db.prepare(`
      SELECT * FROM chat_conversations 
      ORDER BY updated_at DESC
    `);
        const rows = stmt.all();
        return rows.map(row => ({
            id: row.id,
            title: row.title,
            messages: this.getChatMessages(row.id),
            context: JSON.parse(row.context_snapshot || '{}'),
            created_at: row.created_at,
            updated_at: row.updated_at
        }));
    }
    /**
     * Delete chat conversation and all its messages
     */
    deleteChatConversation(conversationId) {
        const deleteMessages = this.db.prepare('DELETE FROM chat_history WHERE conversation_id = ?');
        const deleteConversation = this.db.prepare('DELETE FROM chat_conversations WHERE id = ?');
        const transaction = this.db.transaction(() => {
            deleteMessages.run(conversationId);
            deleteConversation.run(conversationId);
        });
        try {
            transaction();
            return true;
        }
        catch (error) {
            console.error('Failed to delete conversation:', error);
            return false;
        }
    }
    /**
     * Get database statistics including AI tables
     */
    getStats() {
        const teacherCount = this.db.prepare('SELECT COUNT(*) as count FROM teachers').get();
        const courseCount = this.db.prepare('SELECT COUNT(*) as count FROM courses').get();
        const assignmentCount = this.db.prepare('SELECT COUNT(*) as count FROM assignments').get();
        const weightingCount = this.db.prepare('SELECT COUNT(*) as count FROM weighting_settings').get();
        const conversationCount = this.db.prepare('SELECT COUNT(*) as count FROM chat_conversations').get();
        const messageCount = this.db.prepare('SELECT COUNT(*) as count FROM chat_history').get();
        return {
            teachers: teacherCount.count,
            courses: courseCount.count,
            assignments: assignmentCount.count,
            weighting_profiles: weightingCount.count,
            chat_conversations: conversationCount.count,
            chat_messages: messageCount.count
        };
    }
}
